@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Mulish", sans-serif;
}
.navbar {
	background-color: #fff !important;
}

.navbar-brand {
	font-size: 1.3rem;
	color: #565387 !important;
}
.navbar a {
	font-size: 1.3rem;
	text-transform: capitalize;
}
.menu_active {
	font-weight: bold;
	border-bottom: 1px solid #565387;
}

.navbar a:hover {
	color: #3498db !important;
}
@media (max-width: 991px) {
	.navbar-brand {
		font-size: 1.5rem !important;
	}
	.menu_active {
		font-weight: bold;
		border-bottom: none;
	}
}

#header {
	width: 100%;
	height: 80vh;
	margin-top: 70px;
}
#header h1 {
	margin: 0 0 10px 0;
	font-size: 48px;
	font-weight: 700;
	color: black;
}
#header .brand-name {
	color: #3498db;
}
#header h2 {
	color: #484848;
	font-size: 24px;
}
.btn-get-started {
	font-weight: 500;
	font-size: 16px;
	letter-spacing: 1px;
	display: inline-block;
	padding: 10px 30px;
	border-radius: 50px;
	transition: 0.5s;
	color: #3498db;
	border: 2px solid #3498db;
	text-decoration: none;
}
form {
	border: 2px solid #3498db;
	padding: 10px 30px;
	border-radius: 20px;
	box-shadow: 0 0 15px 15px #dadddf;
}
label {
	width: 100%;
	height: 5vh;
	font-weight: bold;
	font-size: large;
	text-align: center;
}
input,
textarea {
	resize: none;
	padding: 10px 30px !important;
	font-weight: 500;
	font-size: 16px;
	letter-spacing: 1px;
	border-radius: 20px !important;
	transition: 0.5s;
	color: #3498db;
	border: 2px solid #3498db !important;
	text-decoration: none;
}
.btn-get-started:hover {
	background: #3498db;
	color: #fff;
}
.btndiv {
	width: 100%;

	text-align: center;
	margin: auto;
}
.header-img {
	text-align: center;
}
#header .animated {
	animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down {
	0% {
		-webkit-transform: translateY(10px);
		        transform: translateY(10px);
	}
	100% {
		-webkit-transform: translateY(-10px);
		        transform: translateY(-10px);
	}
}
@keyframes up-down {
	0% {
		-webkit-transform: translateY(10px);
		        transform: translateY(10px);
	}
	100% {
		-webkit-transform: translateY(-10px);
		        transform: translateY(-10px);
	}
}

